<template>
  <div class="container">
    <Sidebar active="dash" />
    <main>
      <h1>All Endpoints</h1>
      <section>
        <div v-for="(item, index) in endpoints" :key="index" class="tabs">
          <h4>/ {{ item.route }}</h4>
          <ion-icon
            @click="query(item.route)"
            style="color: #00ff00"
            name="flash-outline"
          ></ion-icon>
          <ion-icon
            @click="edit(item.route)"
            style="color: #00b2ff"
            name="create-outline"
          ></ion-icon>
          <ion-icon
            @click="remove(item.route)"
            style="color: #ff7a00"
            name="trash-outline"
          ></ion-icon>
        </div>
        <div class="tabs">
          <h4>Add New</h4>
          <ion-icon
            @click="add"
            style="color: #00ff00"
            name="add-outline"
          ></ion-icon>
        </div>
      </section>
    </main>
  </div>
</template>

<script>
import Sidebar from "../components/Sidebar.vue";

export default {
  name: "Dashboard",
  components: {
    Sidebar,
  },
  mounted() {
    this.getData();
  },
  data() {
    return {
      endpoints: [],
    };
  },
  computed: {
    token() {
      return this.$store.state.token;
    },
  },
  methods: {
    getData() {
      fetch(`${process.env.VUE_APP_URI}api/${this.token}`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
      })
        .then((res) => res.json())
        .then((data) => {
          this.$store.dispatch("setData", data);
          this.endpoints = data;
        });
    },
    remove(name) {
      fetch(`${process.env.VUE_APP_URI}routes/del`, {
        method: "DELETE",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ id: this.token, route: name }),
      })
        .then((response) => {
          this.getData();
        })
        .catch((err) => {
          console.error(err);
        });
    },
    edit(name) {
      this.$router.push({ name: "Update", params: { route: name } });
    },
    query(name) {
      this.$router.push({ name: "Query", params: { route: name } });
    },
    add() {
      this.$router.push({ name: "Add" });
    },
  },
};
</script>

<style lang="scss" scoped>
.container {
  display: flex;
}
main {
  width: 100%;
  h1 {
    padding: 30px 0 0 30px;
    text-align: left;
    font-weight: 600;
    height: 10%;
  }
}
section {
  height: 90%;
  padding: 30px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-auto-rows: 70px;
  gap: 40px;
  overflow-x: hidden;
  overflow-y: auto;
}
.tabs {
  padding: 10px;
  background: #202020;
  border-radius: 10px;
  display: flex;
  align-items: center;
  h4 {
    flex: 2;
    font-weight: 500;
  }
  ion-icon {
    flex: 0.5;
    font-size: 1.5rem;
    cursor: pointer;
    padding: 10px;
    transition: all 100ms linear;
    &:hover {
      transform: scale(1.2);
    }
  }
}
</style>